<template>
  <div class="rating" v-if="rating">
    <template v-if="full === 0">
      <span>暂无评分</span>
    </template>
    <template v-else>
      <template v-for="(n,i) in full">
        <span class="star-full"></span>
      </template>
      <template v-for="n in half">
        <span class="star-half"></span>
      </template>
      <template v-for="n in gray">
        <span class="star-gray"></span>
      </template>
      <span class="average">{{rating.average}}</span>
    </template>
    <slot name="ratingsCount"></slot>
  </div>
</template>

<script>
export default {
  name: 'Rating',
  props: {
    rating: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      full: 0,
      half: 0,
      gray: 0
    }
  },
  created () {
    let average = this.rating.average
    this.full = parseInt(average / 2)
    this.half = average % 2 === 0 ? 0 : 1
    this.gray = 5 - this.full - this.half
  }
}
</script>

<style lang="scss" scoped>
.rating {
  margin-top: 0.1rem;
  font-size: .6rem;
  color: #aaa;
  text-align: center;
}

.star-full {
  display: inline-block;
  width: .5rem;
  height: .5rem;
  background-image: url(data:image/png;base64,);
  background-size: cover;
}

.star-gray {
  display: inline-block;
  width: .5rem;
  height: .5rem;
  background-image: url(data:image/png;base64,);
   background-size: cover;
}

.star-half {
  display: inline-block;
  width: .5rem;
  height: .5rem;
  background-image: url(data:image/png;base64,);
  background-size: cover;
}

.average {
  padding-right: 0.8rem;
}
</style>



