<template>
<div class="header">
  <div class="header-top">
    <h1>
      <router-link to='/'><img src="data:image/png;base64," alt=""></router-link>
    </h1>
    <ul>
      <li v-for="(n,i) in nav" :key="i" @click='toPage(i)'>{{ n }}</li>
    </ul>
    <span class="router" @click="search"></span>
  </div>
  <Search v-if="show" @closeSearch='close'/>
</div>
</template>
<script>
import Search from './search'
export default {
  components:{Search},
  name:'Header',
  data(){
    return {
      nav:['电影','图书','广播','小组'],
      show:false
    }
  },
  methods:{
    search(){
      this.show = true
    },
    close(d){
      this.show = false
    },
    toPage(i){
      if(i == 0){
        this.$router.push({
          path:'/movie'
        })
      }
      if(i == 1){
        this.$router.push({
          path:'/m'
        })
      }
    }
  }
}
</script>
<style lang="scss" scoped>

  @import '@/assets/css/scss.scss';
  .header{
    width: 100%;
    padding: .5rem 0;
    border-bottom: 1px solid #f3f3f3;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background: #fff;
    .header-top{
      width: $width;margin: 0 $auto;
      display: $flex;
      h1{
        flex-grow: 1;
        a{
          img{
            width: 2.8rem;
            height: 1.3rem;
            margin-top: .2rem;
            display: $block;
          }
        }
      }
      ul{
        display: $flex;
        margin-right: $rem;
        @for $i from 1 to 5{
          li:nth-child(5n+#{$i}){
            color:nth($fontColor,$i);
            font-size: .8rem;
            margin:0 .3rem;
            line-height: 1.5rem;
          }
        }
      }
      .router{
        display: $block;
        width: 1.5rem;height: 1.1rem;
        background: url(data:image/png;base64,) no-repeat;
        background-size: cover;
        margin-top: .35rem;
      }
    }
    
  }
</style>





